.conteudoContainer {
    position: relative;
    text-align: center;
    background-color: var(--secondary-color);
    color: var(--main-color);
    padding: 0 1.5rem 6.5rem;
}

.texto404 {
    display: block;
    color: var(--main-color);
    font-family: var(--main-color);
    font-size: 6rem;
    font-weight: 700;
    margin-bottom: 2rem;
}

.titulo {
    font-family: var(--main-color);
    font-size: 3rem;
    margin-bottom: 2rem;
}

.paragrafo {
    font-size: 1.5rem;
    line-height: 1.75rem;
}

.botaoContainer {
    text-align: start;
    margin-top: 3.5rem;
    margin-left: 30vw;
}

.espacoEmBranco {
    height: 30vh;
}

@media (max-width: 1100px) {    
    .texto404 {
        margin-bottom: 2.25rem;
    }

    .botaoContainer {
        margin-top: 2.75rem;
        margin-left: 15vw;
    }


    .espacoEmBranco {
        height: 30vw;
    }
}

@media (max-width: 744px) {
    .conteudoContainer {
        padding: 3rem 1rem 10rem;
    }

    .texto404 {
        font-size: 4rem;
    }

    .titulo {
        font-size: 2.25rem;
        line-height: 3rem;
    }

    .paragrafo {
        font-size: 1.125rem;
        line-height: 1.3125rem;
    }

    .botaoContainer {
        margin: 2.5rem 0 0;
        text-align: center;
    }

    .espacoEmBranco {
        height: 5vw;
    }
}