.weaponModeloContainer {
    color: var(--main-color);
    position: relative;
}

.fotoCapa,.titulo {
    height: 204px;
}

.fotoCapa {
    width: 100%;
    background-size: cover;
    filter: brightness(90%);
    opacity: 0.3;
    position: absolute;
    top: 0;
    z-index: -1;
}

.titulo {
    font-family: var(--main-color);
    padding: 0 7.5rem;
    font-size: 4rem;
    display: flex;
    align-items: center;
}

.weaponConteudoContainer {
    padding: 2.5rem 7.5rem 4.5rem;
}

@media (max-width: 744px) {
    .weaponConteudoContainer {
        padding: 2rem 1.5rem 1.5rem;
    }
}

@media (max-width: 744px) {

    .fotoCapa,.titulo {
        height: 104px;
    }

    .titulo {
        padding: 0 1rem;
        font-size: 2rem;
    }

    .weaponConteudoContainer {
        padding: 2rem 1rem 1.5rem;
    }
}