.weapon_title{
    border: 1px solid black;
    font-size: 22px;
}

.card_weapon{
    margin: auto;
    width: 80%;
    margin-bottom: 2%;
}


.weapons {
    padding: 0 6vw 3.625rem;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 1.5rem;
}

@media (max-width: 1100px) {
    .weapons {
        margin-top: 0;
        padding: 2rem 1.5rem 3.625rem;
    }
}

@media (max-width: 744px) {
    .weapons {
        padding: 2rem 1rem 3rem;
    }
}